          class MenuTop extends React.Component {
              constructor(props) {
                  super(props)
                  this.state = {
                    showMenu: false,
                    showMobileMenu: false
                  }

                  this.onClick = this.onClick.bind(this);
                  this.onClickSecond = this.onClickSecond.bind(this);

              }

              componentDidMount(){
                    // if(this.props.location.pathname == '/') {
                    //     return this.setState( prevState => ({
                    //         showMenu: !prevState.showMenu
                    //     }), () => { console.log(this.state)});
                    // }$

                    console.log(this.props.location.pathname);

                        if(this.props.location.pathname !== '/') {
                            return this.setState( prevState => ({
                                showMenu: !prevState.showMenu
                            }), () => { console.log(this.state)});
                        }

              }

              onClick(){

                    console.log(document.querySelector('.menutop'));
                    document.querySelector('.menutop').style.transform = 'translateY(-100%)';

              }

              onClickSecond(){
                  this.setState(prevState => ({
                    showMobileMenu: !prevState.showMobileMenu
                  }))
                console.log('clicked')
              }

            
          render() {
            console.log(this.props);
            
            if(this.state.showMenu) {
                return(
                    <StyledUl className={'menutop'} style={{transform: 'translateY(0%)'}}>
                    <li><StyledLink onClick={this.onClick}  style={{fontFamily: 'DrukCond-Super', fontSize: '32px'}} to="/" >GEMEINDEHAUS</StyledLink><span className={'mobile-menu-burger'} onClick={this.onClickSecond}>MENU</span></li>
                    <li onClick={this.onClickSecond} className={this.state.showMobileMenu ? 'show' : 'hide'}><StyledLink activeStyle={{textDecoration: 'underline'}}   to="/workspaces" state={{from: this.props.location.pathname }}>WORKSPACES</StyledLink></li>
                    <li onClick={this.onClickSecond} className={this.state.showMobileMenu ? 'show' : 'hide'}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/about" state={{from: this.props.location.pathname }}>ABOUT</StyledLink></li>
                    <li onClick={this.onClickSecond} className={this.state.showMobileMenu ? 'show' : 'hide'}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/stpauli" state={{from: this.props.location.pathname }}>ST. PAULI</StyledLink></li>
                    <li onClick={this.onClickSecond} className={this.state.showMobileMenu ? 'show' : 'hide'}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/kontakt" state={{from: this.props.location.pathname }}>KONTAKT</StyledLink></li>
                    </StyledUl>  
                )
            } else {
                return(
                    <StyledUl className={'menutop'} style={{transform: 'translateY(-100%)'}}>
                    <li><StyledLink onClick={this.onClick}  style={{fontFamily: 'DrukCond-Super', fontSize: '32px'}} to="/" >GEMEINDEHAUS</StyledLink><span onClick={this.onClickSecond}>Menu</span></li>
                    <li onClick={this.onClickSecond} className={this.state.showMobileMenu ? 'show' : 'hide'}><StyledLink activeStyle={{textDecoration: 'underline'}}   to="/workspaces" state={{from: this.props.location.pathname }}>WORKSPACES</StyledLink></li>
                    <li onClick={this.onClickSecond} className={this.state.showMobileMenu ? 'show' : 'hide'}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/about" state={{from: this.props.location.pathname }}>ABOUT</StyledLink></li>
                    <li onClick={this.onClickSecond} className={this.state.showMobileMenu ? 'show' : 'hide'}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/stpauli" state={{from: this.props.location.pathname }}>ST. PAULI</StyledLink></li>
                    <li onClick={this.onClickSecond} className={this.state.showMobileMenu ? 'show' : 'hide'}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/kontakt" state={{from: this.props.location.pathname }}>KONTAKT</StyledLink></li>
                    </StyledUl>  
                    )
            }
      }
          }


export default MenuTop